module.exports = PingFactory;

PingFactory.$inject = ['$http'];
function PingFactory($http) {
  return {
    get: get,
    post: post,
    setCookie: setCookie
  };

  function get() {
    return $http.get("//localhost:8081" + '/ping').
      then(function (response) {
        return response.data;
      });
  }

  function post() {
    return $http.post("//localhost:8081" + '/ping').
      then(function (response) {
        return response.data;
      });
  }

  function setCookie(name, value) {
    return $http.post("//localhost:8081" + '/ping/cookie', { name: name, value: value }, { withCredentials: true }).
      then(function (response) {
        return response.data;
      });
  }
}
